
  .carouselParent{
    top:0px;
    height: calc(100vh - 50px);
    min-width:360px;
    width: 50vw;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: rgb(30, 39, 39);
    z-index:0;
  }
  .slideshowImage{
    height:auto;
    width: 50vw;
  }
  .slideshowImageMobile{
    height: 50vh;
    width: auto;
    max-height: calc(100vw*.75);
  }
  .carouselParentMobile{
    height:100vh;
    min-width:360px;
    max-height: calc(100vw*.75);
    width: 100vw;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: rgb(32, 32, 32);
  }