.parentContainerBuilding{
    height: calc(100vh - 50px);
    background-color: rgb(252, 249, 247);
    z-index: 0;
    display: flex;
    flex-direction: row-reverse;
    flex-wrap: nowrap;
    justify-content: flex-start;
    align-items: flex-start;

  }
  .parentContainerBuildingMobile{
    height: calc(100vh - 50px);
    background-color: rgb(252, 249, 247);
    z-index: 0;
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    justify-content: flex-start;
    align-items: flex-start;
    bottom:0px;

  }
  .carousel{
    height: 100%;
    width: 100%;
    max-width: 100vw;
    height: calc(65vh - 50px);
    min-width:360px;
    z-index:0;
  }
  .carouselParent{
 display: fixed;
    height: calc(100vh - 50px);
    min-width:360px;
    width: 50vw;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: rgb(30, 39, 39);
    z-index:0;
  }
  .slideshowImage{
    height:auto;
    width: 50vw;
  }
  .slideshowImageMobile{
    height: 50vh;
    width: auto;
    max-height: calc(100vw*.75);
  }
  .carouselParentMobile{
    height:100vh;
    min-width:360px;
    max-height: calc(100vw*.75);
    width: 100vw;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: rgb(32, 32, 32);
  }
  
  .designInfoParent{
    min-width: 360px;
    height:100%;
    width: 50vw;
   display: flex;
   flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
   
  }
  .buildingP{

    font-size: min(1.2vw, 16px);
    margin:0px;
    text-align: left;
    line-height: 140%;
  }
  .buildingPMobile{

    margin:5px;
    text-align: left;
    line-height: 140%;
  }
  .exteriorParent{
    height: 100%;
    width:100%;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
   text-align: center;
    padding:10px;
 background-color: rgb(219, 213, 209);
  }
  .floorPlanParent{

    height:100%;
    width:100%;
   display: flex;
   flex-direction: column;
   flex-wrap: none;
   align-items: center;
   justify-content: space-evenly;
   text-align: center;
    padding:10px;
  }
  .floorPlanParentMobile{
    height:100%;
    width:100%;
   display: flex;
   flex-direction: column;
   flex-wrap: none;
   align-items: center;
   justify-content: flex-start;
   text-align: center;
    padding:10px;
    font-family: Cambria,  Times, 'Times New Roman', serif;
 
  }
  .buildingHeader{
    width:100%;
    text-align: center;
  }
  
 
  .downloadLinkContainer{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    margin-right: 70%;
    width:100%;
  }
  .downloadLink{
    margin:5px;
  }
  .leftButton{
    position: relative;
   left:calc(-50% + 20px);
   bottom: calc(25% + 30px);
   height:30px;
    opacity: .6;
  }
  .leftButton:hover{
    position: relative;
   left:calc(-50% + 20px);
   bottom: calc(25% + 30px);
   height:31px;
    opacity: 1;
  }

  .rightButton{
    position: relative;
   left:calc(50% - 20px);
   bottom: calc(25% + 60px);
   height:30px;
    opacity: .6;
    transform: rotate(180deg);
  }
  .rightButton:hover{
    position: relative;
    left:calc(50% - 20px);
   bottom: calc(25% + 60px);
   height:31px;
    opacity: 1;
  }