.sponsorsParent{
    height: calc(100vh - 50px);
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.sponsorsContainer{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: center;
}
.logoContainer{
    width:100%;
    max-width: 300px;
    height:auto;
    min-width: 100px;
}
.logo{
    width:100%;
    max-width: 300px;
    height:auto;
    min-width: 100px;
}
