/*
heading color: #009874
orange/red : #dd4124
light blue: #0dd6f0
green: #00987
background grey: #343a40 
*/
body{
  min-height: -webkit-fill-available;
  width:100%;
  height:100vh;
  font-size: 100%;
 
}
.flipPage{
  
  width:100%;
  height: calc(100vh - 50px);
  padding:0px;
  position: fixed;
  bottom:0px;
}
.topPage{
 height: calc(100vh - 50px);
  width:100%;
  background-image: url("./images/oldPaper.jpg");
  background-repeat: no-repeat;
  background-size:100vw 65vh;
  background-position:top;
  z-index: 0;
}




h1{
  margin:0px;

}
.arrowContainer{
  display:flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: fixed;
  bottom:0px;
  right:0px;
  height:auto;
  background-color:transparent;
}
.arrow{
width: 30px;
margin:3px;
margin-right: 7px;
opacity: .7;
}
.pageNumber{
  margin-right: 5px;
}
.homeTextParent{
  background-color: #343a40de;
  width:80vw;
  height:auto;
  padding:5vh;
  margin-bottom: 8vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  line-height: 140%;
  font-family:Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
  font-size: 17px;
  text-align: center;
}
.homeTextParentMobile{
  background-color: #343a40ce;
  width:100vw;
  height:auto;
  padding:5vh;
  margin-bottom: 13vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  line-height: 140%;
  font-family:Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
  font-size: 17px;
  text-align: center;

}
.homeHeading{
  text-align: center;
   color: rgb(255, 255, 255);
   font-size: 3.5vh;
   width:50%;
   min-width: 200px;
   font-family: 'Tangerine', serif;
   font-size: 40px;
   letter-spacing: 0px;
   border-bottom: solid 1px #dd4124;
   padding: 7px;
  
}
.homeP{
  text-align: center;
  color: white;

}
.missionStatement{
  color: white;
}
.donateButton{
  width:120px;
  height:3em;
  border-radius: 2em;
  background: #74a59e;
  font-size: 1em;
  font-weight: bold;
  color:white;
 
}
.donateButton:hover{
  transform: translateY(-2px);
}
.drawing3d{
  background-image: url("./images/3ddrawings4-3.png");
  background-position: top;
  background-position-y: 3vh;
  background-repeat: no-repeat;
  background-size: 50%;
  width:100vw;
  height: calc(100vh - 50px);
  display: flex;
  justify-content: flex-end;
  flex-direction: column;
  align-items: center;
}
.drawing3dMobile{
  background-image: url("./images/3ddrawings4-3.png");
  background-position: top;
  background-position-y: 5vh;
  background-repeat: no-repeat;
  background-size: contain;
  width:100vw;
  height:calc(100vh - 50px);
  display: flex;
  flex-direction: column;
  justify-content:flex-end;
  align-items: center;
}