.parentContainerMembers{
    height:100vh;
    width:100vw;
    position: relative;
    bottom:0px;
    background-color: white;
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
    z-index: 0;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
    flex-wrap: wrap;
  }
  .pageTitle{
    position:fixed;
    font-family: 'Tangerine', serif;
    font-size: 40px;
  }
  .pageTitleMobile{
    position:absolute;
    width:100%;
    font-family: 'Tangerine', serif;
    font-size: 40px;
    top:10px;
    text-align: center;
  }
